
/**
 * vuex管理登陆状态，具体可以参考官方登陆模板示例
 */
import {
	mapMutations
} from 'vuex';
import Vue from 'vue'

export default {
	data(){
		return{
			wxCode: null,
		}
	},
	methods: {
		...mapMutations(['login','setToken']),
		async loginRequest(userinfo){
			let value = {code: this.wxCode,nickName: userinfo.nickName,avatarUrl: userinfo.avatarUrl};
			let result = await this.$request.ModelUser.login(value);
			if(result.code == 'ok' && result.data){
				let data = result.data;
				let userInfoVo = {
					id: data.userId,
					mobile: data.mobile,
					nickname: userinfo.nickName,
					portrait: userinfo.avatarUrl,
					token: data.token
				};
				this.setToken(data.token);
				this.login(userInfoVo);
			}else{
				this.$api.msg(result.msg);
			}
		}
	},
	onLaunch: function() {
		/* =========== start login ======== */
		let userInfo = uni.getStorageSync('userInfo') || '';
		if(userInfo.id){ //已经登陆过
			//更新登陆状态
			uni.getStorage({
				key: 'userInfo',
				success: (res) => {
					this.login(res.data);
				}
			});
		}else{
			var that = this;
			uni.login({
			  provider: 'weixin',
			  success: function (loginRes) {
				that.wxCode = loginRes.code;
				
				uni.getUserInfo({
				    provider: 'weixin',
				    success: function (infoRes) {
				       console.log('用户昵称为：' + infoRes.rawData);
					   that.loginRequest(infoRes.rawData);
				    }
				});
				
			  }
			});
		}
		/* =========== end login ========== */
		uni.getSystemInfo({
			success: function(e) {









				Vue.prototype.StatusBar = e.statusBarHeight;
				let custom = wx.getMenuButtonBoundingClientRect();
				Vue.prototype.Custom = custom;
				Vue.prototype.CustomBar = custom.bottom + custom.top - e.statusBarHeight;





			}
		})
	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	}
}
